@use 'global' as *;

.article-card {
  box-shadow: 2px 2px 10px 0px rgba(0, 0, 0, 0.25);
}

.article-card:not(:first-child){
  margin-top: rem(24);
}

.article-card__inner{
  display: flex;
  gap:rem(16);
  padding: rem(16);
}

.article-card__image {
  width: rem(121);
  aspect-ratio: 121 / 90;
}

.article-card__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.article-card__body {
  width: rem(125);
}

.article-card__date {
  font-family: $english-font;
  font-size: rem(12);

}

.article-card__title {
  font-family: $sans;
  font-size: rem(16);

}

